import axios from 'axios'
// axios
const ajax = axios.create({
    //baseURL: 'http://app.superkeep.io/PreDataCenter/', // apibase_url
    //baseURL: 'http://192.168.2.101:8088/DataCenter/',
    baseURL: 'http://43.228.71.25:8001/PreDataCenter/', 
    timeout: 5000 // 
})
// respone
ajax.interceptors.response.use(
    response => {
    return response.data
})
export default ajax


